.intro-container {
  width: 100%;
}

.introduction-section {
  text-align: center;
}

.intro-heading {
  background-color: #4a4947;
  margin-bottom: 20px;
  color: #faf7f0;
}

.intro-heading h2 {
  font-size: 28px;
  font-weight: bold;
  padding: 20px 0;
}

.intro-text {
  width: 90%;
  margin: 0 auto;
  padding: 20px 0;
}

.intro-text p {
  font-size: 18px;
  color: #333;
  line-height: 1.8;
  text-align: justify;
}

@media (max-width: 768px) {
  .intro-text {
    width: 100%;
  }
}
